import React from 'react';
import { Wrapper, Status } from "@googlemaps/react-wrapper";
import { MyMap } from './components/map';
import './App.css';
import { Col, Container, Row } from 'react-bootstrap';
import { SideBar } from './components/sidebar/sidebar';

const center = { lat: 47.6101, lng: -122.2015 };
const zoom = 12;
const PROD_API_KEY = "AIzaSyAPgOQy-WXl0VH0cbcnA5dE3lmGwEpkQGE"
const NO_PADDING_STYLE = {
  paddingLeft: 0,
  paddingRight: 0
}

const render = (status: Status) => {
  switch (status) {
    case Status.LOADING:
      return <p>Loading...</p>;
    case Status.FAILURE:
      return <p>Failed</p>;
    case Status.SUCCESS:
      return <MyMap center={center} zoom={zoom} />;
  }
};

const apiKey = process.env.NODE_ENV === 'development' ? "" : PROD_API_KEY
const MapWrapper = () => <Wrapper apiKey={apiKey} render={render} />;

function App() {
  return <Container fluid style={NO_PADDING_STYLE}>
    <Row style={NO_PADDING_STYLE}>
      <Col style={{maxWidth: '2rem', ...NO_PADDING_STYLE}}>
        <SideBar />
      </Col>
      <Col 
        style={{paddingLeft: 0}}
        // md={11.5} lg={11.5} xl={11.5} xxl={11}
      >
        <MapWrapper />
      </Col>
    </Row>
  </Container>
}

export default App;
