import { makeAutoObservable, observable, action } from "mobx";
import { createContext } from "react"
import { OsmPlace } from "../types/commontypes";
class SearchBoxStore {
    @observable searchInput: string = ''
    @observable places: OsmPlace[] = []

    constructor() {
        makeAutoObservable(this)
    }

    @action changeSearchInput(newInput: string) {
        this.searchInput = newInput
    }

    @action changeResults(newPlaces: OsmPlace[]) {
        this.places = newPlaces
    }

    @action clearResults() {
        this.places = []
    }
}

export default createContext(new SearchBoxStore())
export {SearchBoxStore}
