import React, { useContext } from "react";
import { Accordion, Button, ButtonToolbar, ButtonGroup } from "react-bootstrap";
import { observer } from "mobx-react-lite";
import { MapStore, default as MapStoreContext } from "../../stores/mapstore";

const MarkerWidget = observer<any, any>(({id}: {id: string}) => {
    const mapStore: MapStore = useContext(MapStoreContext);
    const marker = mapStore.markers.get(id)
    if (marker == null) {
        return null
    } 
    return <Accordion.Item eventKey={id}>
        <Accordion.Header>{marker.displayName}</Accordion.Header>
        <Accordion.Body>
            <ButtonToolbar className="mb-1" aria-label="Toolbar with Button groups">
                <ButtonGroup className="me-2" aria-label="First group">
                    <Button onClick={() => mapStore.toggleMarkerVisible(id)}>
                        {marker.isVisible ? 'Hide' : 'Show'}
                    </Button>
                    <Button onClick={() => mapStore.removeMarker(id)}>
                        Remove
                    </Button>
                </ButtonGroup>
            </ButtonToolbar>
        </Accordion.Body>
    </Accordion.Item>
})

export {MarkerWidget}
