import { action, computed, makeAutoObservable, observable } from "mobx";
import { Point } from "./commontypes";
const MIN_OPACITY = 0.001
const MAX_OPACITY = 0.999
const DEFAULT_OPACITY = 0.2
const DEFAULT_STROKE_COLOR = "#FF0000"
const DEFAULT_FILL_COLOR = "#FFFFFF"
class Polygon {
    @observable isVisible: boolean
    @observable fillColor: string = DEFAULT_FILL_COLOR
    @observable strokeColor: string = DEFAULT_STROKE_COLOR
    @observable fillOpacity: number = DEFAULT_OPACITY
    gmapPolygon: google.maps.Polygon

    constructor(public id: string, public displayName: string, public points: Point[]){
        makeAutoObservable(this)
    
        this.isVisible = true
        this.gmapPolygon = new google.maps.Polygon({
            paths: points,
            strokeColor: this.strokeColor,
            strokeOpacity: 0.8,
            strokeWeight: 2,
            fillColor: this.fillColor,
            fillOpacity: this.fillOpacity,
        });
    }

    @action setStrokeColor(newColor: string) {
        this.strokeColor = newColor
        this.gmapPolygon.setOptions({strokeColor: this.strokeColor})
    }

    @action setFillColor(newColor: string) {
        this.fillColor = newColor
        this.gmapPolygon.setOptions({fillColor: this.fillColor})
    }

    @action setFillOpacity(newOpacityInput: string) {
        const parsedInput = Number(newOpacityInput)
        if (isNaN(parsedInput)) {
            return
        }
        var opacity = parsedInput / 100
        if (opacity < MIN_OPACITY) {
            opacity = MIN_OPACITY
        } else if (opacity > MAX_OPACITY) {
            opacity = MAX_OPACITY
        }
        this.fillOpacity = opacity
        this.gmapPolygon.setOptions({fillOpacity: this.fillOpacity})
    }

    @computed get opacityRangeValue(): number {
        return this.fillOpacity * 100
    }
}

export {Polygon}