import React from "react";
import { Col, Row, Button, Offcanvas } from "react-bootstrap";
import { MarkerList } from "../markerlist/markerlist";
import { PolygonList } from "../polygonlist/polygonlist";
import { SearchBox } from "./searchbox/searchbox";

class SideBar extends React.Component<any, {show: boolean}> {
    constructor(props) {
        super(props)
        this.state = {show: false}
    }

    handleShow() {
        this.setState({show: true})
    }

    handleClose() {
        this.setState({show: false})
    }

    render() {
        return <div>
            <Button variant="secondary-outline"
                onClick={() => this.handleShow()}
            >
                <i className="bi bi-three-dots-vertical"></i>
            </Button>
            <Offcanvas show={this.state.show} 
                onHide={() => this.handleClose()}
                scroll={true}
                backdrop={false}
            >
                <Offcanvas.Header closeButton>
                    <Offcanvas.Title>Search for places</Offcanvas.Title>
                </Offcanvas.Header>
                <Offcanvas.Body>
                    <Row>
                        <Col><SearchBox /></Col>
                    </Row>
                    <Row>
                        <Col>
                        <br/>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <PolygonList />
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <br/>
                        </Col>
                    </Row>                    
                    <Row>
                        <Col>
                            <MarkerList />
                        </Col>
                    </Row>
                </Offcanvas.Body>
            </Offcanvas>
        </div>
    }
}

export {SideBar}
