import { makeAutoObservable, observable, action, ObservableMap } from "mobx";
import { createContext } from "react"
import { OsmPlace, Point, OsmBoundingBox } from "../types/commontypes";
import { Polygon } from "../types/polygon";
import {MapPlace} from "../types/place"

class MapStore {
    @observable mapObj?: google.maps.Map
    @observable polygons: ObservableMap<string, Polygon> = observable.map()
    @observable markers: ObservableMap<string, MapPlace> = observable.map()

    constructor() {
        makeAutoObservable(this)
    }

    @action setMap(mapObj: google.maps.Map) {
        this.mapObj = mapObj
    }

    @action addBoundaries(osmPlace: OsmPlace) {
        if (osmPlace.geojson.type ===  "Polygon") {
            osmPlace.geojson.coordinates.forEach((polygon, index) => {
                const id = `polygon:${osmPlace.osm_id}:${index}`
                this.addPolygon(osmPlace, polygon, id)
            })
        } else if (osmPlace.geojson.type ===  "MultiPolygon"){
            osmPlace.geojson.coordinates.forEach((subPolygon, subPolygonIndex) => {
                subPolygon.forEach((polygon, index) => {
                    const id = `polygon:${osmPlace.osm_id}:${subPolygonIndex}:${index}`
                    this.addPolygon(osmPlace, polygon, id)
                })
            })
        }
    }

    @action removePolygon(id: string) {
        const polygon = this.polygons.get(id)
        if (polygon != null) {
            polygon.gmapPolygon.setMap(null)
            this.polygons.delete(id)
        }
    }

    @action togglePolygonVisible(id: string) {
        const polygon = this.polygons.get(id)
        if (polygon != null) {

            polygon.isVisible = polygon.isVisible ? false : true
            this.polygons.set(id, polygon)
        }
    }

    @action addMarker(osmPlace: OsmPlace) {
        const id = `point:${osmPlace.osm_id}`
        if (!this.markers.has(id)) {
            const markerLongitude = Number(osmPlace.lon)
            const markerLatitude = Number(osmPlace.lat)
            if (!isNaN(markerLongitude) && !isNaN(markerLatitude)) {
                const markerPoint: Point = {
                    lat: markerLatitude,
                    lng: markerLongitude
                }
                const newMarker = new MapPlace(id, osmPlace.display_name, markerPoint)
                this.markers.set(id, newMarker)
            }
        }
    }

    @action toggleMarkerVisible(id) {
        const marker = this.markers.get(id)
        if (marker != null) {

            marker.isVisible = marker.isVisible ? false : true
            this.markers.set(id, marker)
        }
    }

    @action removeMarker(id: string) {
        const marker = this.markers.get(id)
        if (marker != null) {
            marker.gmapMarker.setMap(null)
            this.markers.delete(id)
        }
    }

    addPolygon(osmPlace: OsmPlace, polygon, id) {
        if (!this.polygons.has(id)) {
            const points: Point[] = polygon.map(longlat => ({
                lng: longlat[0],
                lat: longlat[1]
            }))
            const newPolygon = new Polygon(id, osmPlace.display_name, points)
            this.polygons.set(id, newPolygon)
        }
    }

    panToOsmBondingBox(bbox: OsmBoundingBox) {
        const latNe = Number(bbox[1])
        const latSw = Number(bbox[0])
        const lngNe = Number(bbox[3])
        const lngSw = Number(bbox[2])
        if (!isNaN(latNe) && !isNaN(latSw) && !isNaN(lngNe) && !isNaN(lngSw)) {
            const sw = {lat: latSw, lng: lngSw}
            const ne = {lat: latNe, lng: lngNe}
            const bounds = new google.maps.LatLngBounds(sw, ne)
            if (this.mapObj != null) {
                this.mapObj.fitBounds(bounds, 10)
            }
        }
    }
}

export default createContext(new MapStore())
export {MapStore}
