import { useContext } from 'react'
import { ListGroup } from 'react-bootstrap'
import {observer} from "mobx-react-lite"
import { MapStore, default as MapStoreContext } from "../../../stores/mapstore";
import { OsmPlace } from '../../../types/commontypes';

const SearchResult = observer<any, any>((props) => {
    const {place}: {place: OsmPlace} = props
    const mapStore: MapStore = useContext(MapStoreContext);
    if (place.class === 'boundary') {
        return <ListGroup.Item>
            <div className="d-flex align-items-center">
                <span style={{paddingRight: "0.5rem"}}>
                    <i className="bi bi-map align-middle"
                        onClick={() => mapStore.panToOsmBondingBox(place.boundingbox)}
                    ></i>
                </span>
                <span>
                    {place.display_name}
                </span>
                <span style={{paddingLeft: "0.5rem"}}>
                    <i className="bi bi-plus-square"
                        onClick={() => mapStore.addBoundaries(place)}
                    ></i>
                </span>
            </div>
        </ListGroup.Item>
    } else if (place.class === 'place') {
        return <ListGroup.Item>
            <div className="d-flex align-items-center">
                <span style={{paddingRight: "0.5rem"}}>
                    <i className="bi bi-geo-alt"
                        onClick={() => mapStore.panToOsmBondingBox(place.boundingbox)}
                    ></i>
                </span>
                <span>
                    {place.display_name}
                </span>
                <span style={{paddingLeft: "0.5rem"}}>
                    <i className="bi bi-plus-square"
                        onClick={() => mapStore.addMarker(place)}
                    ></i>
                </span>
            </div>
        </ListGroup.Item>
    }
    return null
    // return <Row>
    //     <Col>
    //         <Button onClick={() => mapStore.addBoundaries(place)}>
    //             {place.display_name}
    //         </Button>
    //     </Col>
    // </Row>
})

export {SearchResult}
