import {observer} from "mobx-react-lite"
import React, { useContext } from "react";
import { Button, FormControl, InputGroup, Row, Col, ListGroup } from "react-bootstrap";
import { NominatimClient } from "../../../client/nominatim";
import {default as SearchBoxStoreContext, SearchBoxStore}  from "../../../stores/searchboxstore"
import {SearchResult} from "./searchresult"

const placeApi = new NominatimClient()

const handleSearchInputChange = (evt, searchBoxStore: SearchBoxStore) => {
    searchBoxStore.changeSearchInput(evt.target.value)
}

const handleSearchClick = async (searchBoxStore: SearchBoxStore) => {
    const places = await placeApi.search(searchBoxStore.searchInput)
    searchBoxStore.changeResults(places)
}

const renderSearchResults = (searchBoxStore: SearchBoxStore) => {
    if (searchBoxStore.places == null || searchBoxStore.places.length === 0) {
        return null
    }
    const results: any = []
    for (const p of searchBoxStore.places) {
        results.push(<SearchResult place={p} key={p.osm_id}/>)
    }
    return <ListGroup>{results}</ListGroup>
}

const SearchBox = observer(() => {
    const searchBoxStore: SearchBoxStore = useContext(SearchBoxStoreContext);
    return <>
        <Row>
            <Col>
                <InputGroup className="mb-3" >
                    <FormControl
                        className="w-50 form-control border-end-0 border"
                        placeholder="Place of interest"
                        aria-label="Place of interest"
                        aria-describedby="basic-addon2"
                        onChange={(evt) => handleSearchInputChange(evt, searchBoxStore)}
                        value={searchBoxStore.searchInput}
                    />
                    <Button variant="outline-secondary border-start-0 border-end-0 border" id="button-addon2" onClick={() => searchBoxStore.changeSearchInput('')}>
                        <i className="bi bi-x"></i>
                    </Button >
                    <Button className="border-start-0 border"
                        variant="outline-secondary" 
                        id="button-addon2" 
                        onClick={() => handleSearchClick(searchBoxStore)}
                    >
                        <i className="bi bi-search"></i>
                    </Button >
                </InputGroup>
                <Button variant="outline-danger" onClick={() => searchBoxStore.clearResults()}>
                    Clear search results
                </Button >
            </Col>
        </Row>
        <Row>
            <Col>
                    {renderSearchResults(searchBoxStore)}
            </Col>
        </Row>
    </>
})

export {SearchBox}
