import { observer } from "mobx-react-lite";
import React, { useContext } from "react";
import { Accordion } from "react-bootstrap";
import { MapStore, default as MapStoreContext } from "../../stores/mapstore";
import { PolygonWidget } from "./polyconwidget";

const PolygonList = observer(() => {
    const mapStore: MapStore = useContext(MapStoreContext);
    const widgets: any[] = []
    mapStore.polygons.forEach((v, k) => {
        widgets.push(<PolygonWidget key={k} id={k}  />);

    })
    return <Accordion>
        {widgets}
    </Accordion>
})

export {PolygonList}